	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Turck MMCache for PHP","date":"14 Oct 2003","slug":"/2003-10-14-turck-mmcache-for-php/","tags":"Y2003"},"html":"<p><a title=\"Better than PHP Accelerator? We'll find out...\" href=\"http://h-blog.org/archives/000558.html\">Turck MMCache for PHP</a></p>"},"next":{"fields":{"title":"Spiegel Online: Whale found in Hamburg Harbor","date":"14 Oct 2003","slug":"/2003-10-14-spiegel-online-whale-found-in-hamburg-harbor/"}},"previous":{"fields":{"title":"Amango","date":"15 Oct 2003","slug":"/2003-10-15-amango/"}}},{"node":{"fields":{"title":"Spiegel Online: Whale found in Hamburg Harbor","date":"14 Oct 2003","slug":"/2003-10-14-spiegel-online-whale-found-in-hamburg-harbor/","tags":"Y2003"},"html":"<p><a title=\"12 tons, 12 meters long, dead, and smelly\" href=\"http://www.spiegel.de/wissenschaft/erde/0,1518,269754,00.html\">Spiegel Online: Whale found in Hamburg Harbor</a></p>"},"next":{"fields":{"title":"Smarty","date":"14 Oct 2003","slug":"/2003-10-14-smarty/"}},"previous":{"fields":{"title":"Turck MMCache for PHP","date":"14 Oct 2003","slug":"/2003-10-14-turck-mmcache-for-php/"}}},{"node":{"fields":{"title":"Smarty","date":"14 Oct 2003","slug":"/2003-10-14-smarty/","tags":"Y2003"},"html":"<p>Learning a templating system for PHP has long been something I knew I *should* do, but had been long put off because I knew it would be uncomfortable, like cleaning my desk or going to the dentist. No matter which system I would chose, there would be some overhead in learning and in rearranging my work. </p>\n<p>Finally, inspired last week the implementation of templates at <a href=\"http://www.blogg.de\">blogg.de</a>, I jumped into the <a title=\"Smarty : Template Engine\" href=\"http://smarty.php.net/\">Smarty</a> template engine. And as I implemented my first template, I promptly made my first newbie mistake, namely confusing 'caching' with 'pre-compiling'. I set my language-specific entry page to be cached, and wondered why my site was suddenly only in German. After a slight bout of panic, I checked the docs and found I could set <a href=\"http://smarty.php.net/manual/en/caching.multiple.caches.php\">separate caches</a> for German and English. </p>\n<p>I knew this would be as much fun as having my teeth cleaned. But unlike going to the dentist, hopefully I only have to learn Smarty once, and can then use it for all my little PHP projects.</p>"},"next":{"fields":{"title":"Klingon is not in Unicode","date":"14 Oct 2003","slug":"/2003-10-14-klingon-is-not-in-unicode/"}},"previous":{"fields":{"title":"Spiegel Online: Whale found in Hamburg Harbor","date":"14 Oct 2003","slug":"/2003-10-14-spiegel-online-whale-found-in-hamburg-harbor/"}}}],"pathPrefix":"","first":false,"last":false,"index":610,"pageCount":1101}}

